import React from 'react';

export default function FormGroup (props) {
  const { title, isRequired } = props;
  return (
    <div className='form-group'>
      <label className='col-sm-4 control-label'>{title}&nbsp;
        {isRequired === true &&
          <span className='label label-danger req'></span>}
        {isRequired === false &&
          <span className='label label-default req'></span>}
      </label>
      <div className='col-sm-8'>
        {props.children}
      </div>
    </div>
  );
}
