import React from 'react';
import UserInfoPersonal from './UserInfoPersonal';
import UserInfoCompany from './UserInfoCompany';
import FormGroup from '../Shared/FormGroup';
import Select from 'react-select';
import AlertText from '../Shared/AlertText';
import uuidv4 from 'uuid/v4';
import * as Const from '../../Const';
import * as Questionnaires from '../../Questionnaires';
import * as InputHelper from '../../Utils/EntryInputHelper';

export default class Entry extends React.Component {
  render () {
    const {
      ShopUnitInfo,
      StartDateItem,
      CustomerType,
      OverallItem,
      OverallHandlers,
      SubmitHandler
    } = this.props;

    // console.log(OverallItem);
    const shopName = (ShopUnitInfo === undefined ? '' : ShopUnitInfo.shopName);
    const unitTypeName = (ShopUnitInfo === undefined ? '' : ShopUnitInfo.unitTypeName);
    const paymentMethodClickHandler = OverallHandlers.paymentMethodClickHandler;

    return (
      <div className='row'>
        <div className='col-md-10 col-md-offset-1 col-sm-10 col-sm-offset-1'>
          <div className='form-horizontal'>
            <FormGroup title='' isRequired>
              <input
                type='text'
                name='kibou_tenpo'
                size='30'
                maxLength='30'
                className='form-control'
                id='expired_shop'
                placeholder=''
                readOnly
                value={shopName}
              />
            </FormGroup>

            <FormGroup title='' isRequired>
              <input
                type='text'
                name='kibou_room_type_tmp'
                size='30'
                maxLength='30'
                className='form-control'
                id='expired_unittype'
                placeholder=''
                value={unitTypeName}
                readOnly
              />
            </FormGroup>

            <FormGroup title='' isRequired>
              <div className='row'>
                <div className='col-sm-4 col-xs-4'>
                  <div className='input-group'>
                    <select
                      className='form-control'
                      name='riyou_start_y'
                      value={StartDateItem.startDate.year}
                      onChange={StartDateItem.updateStartDateHandler.bind(this, 'year')}
                    >
                      {
                        InputHelper.GetStartYearItems().map(item =>
                          <option key={item} value={item}>{item}</option>
                        )
                      }
                    </select>
                    <span className='input-group-addon'></span>
                  </div>
                </div>
                <div className='col-sm-4 col-xs-4'>
                  <div className='input-group'>
                    <select
                      className='form-control'
                      name='riyou_start_m'
                      value={StartDateItem.startDate.month}
                      onChange={StartDateItem.updateStartDateHandler.bind(this, 'month')}
                    >
                      {
                        InputHelper.GetMonthItems().map(item =>
                          <option key={item} value={item}>{item}</option>
                        )
                      }
                    </select>
                    <span className='input-group-addon'></span>
                  </div>
                </div>
                <div className='col-sm-4 col-xs-4'>
                  <div className='input-group'>
                    <select
                      className='form-control'
                      name='riyou_start_d'
                      value={StartDateItem.startDate.date}
                      onChange={StartDateItem.updateStartDateHandler.bind(this, 'date')}
                    >
                      {
                        InputHelper.GetDayItems().map(item =>
                          <option key={item} value={item}>{item}</option>
                        )
                      }
                    </select>
                    <span className='input-group-addon'></span>
                  </div>
                </div>
              </div>
              {
                StartDateItem.isValid === false &&
                  <AlertText text='' />
              }
            </FormGroup>

            <FormGroup title='' isRequired>
              <div className='btn-group btn-group-justified' data-toggle='buttons'>
                <label className={`btn btn-default ${OverallItem.paymentMethod === Const.PaymentMethod.CreditCard ? 'active' : ''}`}>
                  <input
                    type='radio'
                    name='shiharai_houhou'
                    value={Const.PaymentMethod.CreditCard}
                    autoComplete='off'
                    onClick={paymentMethodClickHandler.bind(this)}
                  /> 
                </label>
                <label className={`btn btn-default ${OverallItem.paymentMethod === Const.PaymentMethod.BankTransfer ? 'active' : ''}`}>
                  <input
                    type='radio'
                    name='shiharai_houhou'
                    value={Const.PaymentMethod.BankTransfer}
                    autoComplete='off'
                    onClick={paymentMethodClickHandler.bind(this)}
                  /> 
                </label>
              </div>
              {OverallItem.paymentMethod === Const.PaymentMethod.BankTransfer &&
                <p className='help-block'></p>}

            </FormGroup>

            <FormGroup title='' isRequired>
              <div className='btn-group btn-group-justified' data-toggle='buttons'>
                <label className={`btn btn-default ${CustomerType === Const.CustomerType.Personal ? 'active' : ''}`} id='btn_kokyaku_personal'>
                  <input type='radio' name='kokyaku_syubetsu' value={Const.CustomerType.Personal} autoComplete='off' onClick={OverallHandlers.customerTypeClickHandler.bind(this)} /> 
                </label>
                <label className={`btn btn-default ${CustomerType === Const.CustomerType.Company ? 'active' : ''}`} id='btn_kokyaku_company'>
                  <input type='radio' name='kokyaku_syubetsu' value={Const.CustomerType.Company} autoComplete='off' onClick={OverallHandlers.customerTypeClickHandler.bind(this)} /> 
                </label>
              </div>
            </FormGroup>

            {CustomerType === Const.CustomerType.Personal && <UserInfoPersonal {...this.props} />}
            {CustomerType === Const.CustomerType.Company && <UserInfoCompany {...this.props} />}

            <FormGroup title='' isRequired>
              <div style={{ paddingTop: '5px', paddingBottom: '5px' }}>
                <input type='file' name='identification1' className='form-control' onChange={OverallHandlers.identificationChangeHandler.bind(this)} />
              </div>
              <div style={{ paddingTop: '5px', paddingBottom: '5px' }}>
                <input type='file' name='identification2' className='form-control' onChange={OverallHandlers.identificationChangeHandler.bind(this)} />
              </div>
            </FormGroup>

            <FormGroup title='' isRequired>
              {
                <Select
                  onChange={OverallHandlers.selectChangeHandler.bind(this, 'usagePeriod')}
                  placeholder=''
                  options={InputHelper.GetUsagePeriodOptions()}
                  isSearchable={false}
                />
              }
            </FormGroup>

            <FormGroup title=''>
              <textarea
                value={OverallItem.otherCampaign}
                onChange={OverallHandlers.updateOverallInfoHandler.bind(this)}
                name='otherCampaign'
                className='form-control'
                maxLength='200'
              />
              <p><small></small></p>
            </FormGroup>

            <FormGroup title='' isRequired>
              {
                Questionnaires.StorageItems.map(item => {
                  const id = uuidv4();
                  return (
                    <div key={id}>
                      <input
                        onChange={OverallHandlers.checkboxClickHandler.bind(this)}
                        checked={OverallItem.storageItems.find(storageItem => storageItem === item) !== undefined ? 'checked' : ''}
                        type='checkbox'
                        name='storageItems'
                        id={`storage_items_${id}`}
                        value={item}
                      />
                      <label htmlFor={`storage_items_${id}`} style={{ fontWeight: 'normal', paddingLeft: '10px' }}>{item}</label>
                    </div>);
                }
                )
              }
            </FormGroup>

            <FormGroup title=''>
              <textarea
                value={OverallItem.storageItemOther}
                onChange={OverallHandlers.updateOverallInfoHandler.bind(this)}
                name='storageItemOther'
                className='form-control'
                maxLength='200'
              />
            </FormGroup>

            <FormGroup title='' isRequired>
              {
                <Select
                  onChange={OverallHandlers.selectChangeHandler.bind(this, 'purpose')}
                  placeholder=''
                  options={InputHelper.GetPurposeOptions()}
                  isSearchable={false}
                />
              }
            </FormGroup>

            <FormGroup title=''>
              <textarea
                value={OverallItem.purposeOther}
                onChange={OverallHandlers.updateOverallInfoHandler.bind(this)}
                name='purposeOther'
                className='form-control'
                maxLength='200'
              />
            </FormGroup>

            <FormGroup title='' isRequired={false}>
              {
                <Select
                  onChange={OverallHandlers.selectChangeHandler.bind(this, 'viewedMedias')}
                  placeholder=''
                  options={InputHelper.GetViewedMediaOptions()}
                  isSearchable={false}
                />
              }
            </FormGroup>

            <FormGroup title=''>
              <textarea
                value={OverallItem.viewedMediaOther}
                onChange={OverallHandlers.updateOverallInfoHandler.bind(this)}
                name='viewedMediaOther'
                className='form-control'
                maxLength='200'
              />
            </FormGroup>

            <FormGroup title='' isRequired>
              {
                Questionnaires.RentalReasons.map(item => {
                  const id = uuidv4();
                  return (
                    <div key={id}>
                      <input
                        onChange={OverallHandlers.checkboxClickHandler.bind(this)}
                        checked={OverallItem.rentalReasons.find(rentalReason => rentalReason === item) !== undefined ? 'checked' : ''}
                        type='checkbox'
                        name='rentalReasons'
                        id={`rental_reason_${id}`}
                        value={item}
                      />
                      <label htmlFor={`rental_reason_${id}`} style={{ fontWeight: 'normal', paddingLeft: '10px' }}>{item}</label>
                    </div>);
                }
                )
              }
            </FormGroup>

            <FormGroup title=''>
              <textarea
                value={OverallItem.rentalReasonOther}
                onChange={OverallHandlers.updateOverallInfoHandler.bind(this)}
                name='rentalReasonOther'
                className='form-control'
                maxLength='200'
              />
            </FormGroup>

            <FormGroup title=''>
              <textarea
                value={OverallItem.otherMessage}
                onChange={OverallHandlers.updateOverallInfoHandler.bind(this)}
                name='otherMessage'
                className='form-control'
                maxLength='200'
              />
              <p><small>No.15&nbsp;&nbsp;</small></p>
            </FormGroup>

            <div className='form-group action'>
              <div className='col-sm-12 col-sm-offset-5'>
                <input
                  onClick={SubmitHandler}
                  type='submit'
                  value=''
                  className='btn btn-warning btn-lg'
                />
              </div>
            </div>
          </div>
        </div>

      </div>
    );
  }
}
