import React from 'react';
import FormGroup from '../Shared/FormGroup';
import PhoneNumber from '../Shared/PhoneNumber';
import PostalCode from '../Shared/PostalCode';
import AlertText from '../Shared/AlertText';
import * as Const from '../../Const';
import * as InputHelper from '../../Utils/EntryInputHelper';

export default class UserInfoPersonal extends React.Component {
  render () {
    const {
      PersonalInfoItem,
      PersonalInfoHandlers
    } = this.props;

    const isAdditionalContactTypeRelative = (PersonalInfoItem.additionalContactType === Const.PersonalAdditionalContactType.Relative);
    const isAdditionalContactTypeCompany = (PersonalInfoItem.additionalContactType === Const.PersonalAdditionalContactType.Company);

    // console.log(PersonalInfoItem, isAdditionalContactTypeRelative, isAdditionalContactTypeCompany);

    return (
      <div id='kokyaku_personal'>
        <FormGroup title='' isRequired>
          <div className='row'>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'></span>
                <input
                  type='text'
                  name='lastName'
                  value={PersonalInfoItem.lastName}
                  onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                  size='20'
                  maxLength='40'
                  className='form-control'
                />
              </div>
            </div>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'></span>
                <input
                  type='text'
                  name='firstName'
                  value={PersonalInfoItem.firstName}
                  onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                  size='20'
                  maxLength='40'
                  className='form-control'
                />
              </div>
            </div>

          </div>
        </FormGroup>

        <FormGroup title='' isRequired>
          <div className='row'>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'></span>
                <input
                  type='text'
                  name='lastNameKana'
                  value={PersonalInfoItem.lastNameKana}
                  onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                  size='40'
                  maxLength='60'
                  className='form-control'
                />
              </div>
            </div>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'></span>
                <input
                  type='text'
                  name='firstNameKana'
                  value={PersonalInfoItem.firstNameKana}
                  onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                  size='40'
                  maxLength='120'
                  className='form-control'
                />
              </div>
            </div>
          </div>
        </FormGroup>

        <FormGroup title='' isRequired>
          <input
            type='email'
            name='kokyaku_email'
            value={PersonalInfoItem.email}
            onChange={PersonalInfoHandlers.updateEmailHandler.bind(this)}
            size='40'
            maxLength='60'
            className='form-control ime-mode-disabled'
          />
          {
            PersonalInfoItem.isValidEmail === false &&
              <AlertText text='' />
          }
        </FormGroup>

        <FormGroup title='' isRequired>
          <input
            type='password'
            name='kokyaku_password'
            // value={PersonalInfoItem.password}
            onChange={PersonalInfoHandlers.updatePasswordHandler.bind(this)}
            size='40'
            maxLength='60'
            className='form-control ime-mode-disabled'
          />
          {
            PersonalInfoItem.isValidPassword === false &&
              <AlertText text='' />
          }
          <p className='help-block'>7</p>
        </FormGroup>

        <FormGroup title='' isRequired>
          <PhoneNumber
            title=''
            phoneType={Const.PhoneType.MobilePhone}
            phone1={PersonalInfoItem.mobilePhone1}
            phone2={PersonalInfoItem.mobilePhone2}
            phone3={PersonalInfoItem.mobilePhone3}
            updatePhoneHandler={PersonalInfoHandlers.updatePhoneHandler}
          />
          {
            PersonalInfoItem.isValidMobilePhone === false &&
              <AlertText text='' />
          }
          <br />
          <PhoneNumber
            title=''
            phoneType={Const.PhoneType.Telephone}
            phone1={PersonalInfoItem.tel1}
            phone2={PersonalInfoItem.tel2}
            phone3={PersonalInfoItem.tel3}
            updatePhoneHandler={PersonalInfoHandlers.updatePhoneHandler}
          />
          {
            PersonalInfoItem.isValidTel === false &&
              <AlertText text='' />
          }
          <p className='help-block'></p>
        </FormGroup>

        <FormGroup isRequired={false}>
          <PhoneNumber
            title='FAX'
            phoneType={Const.PhoneType.Fax}
            phone1={PersonalInfoItem.fax1}
            phone2={PersonalInfoItem.fax2}
            phone3={PersonalInfoItem.fax3}
            updatePhoneHandler={PersonalInfoHandlers.updatePhoneHandler}
          />
          {
            PersonalInfoItem.isValidFax === false &&
              <AlertText text='' />
          }
        </FormGroup>

        <FormGroup title='' isRequired>
          <PostalCode
            postalCode1={PersonalInfoItem.postalCode1}
            postalCode2={PersonalInfoItem.postalCode2}
            updatePostalCodeHandler={PersonalInfoHandlers.updatePersonalPostalCodeHandler}
          />
          {
            PersonalInfoItem.isValidPostalCode === false &&
              <AlertText text='' />
          }
        </FormGroup>

        <FormGroup title='' isRequired>
          <input
            type='text'
            name='address'
            value={PersonalInfoItem.address}
            onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
            size='56'
            maxLength='100'
            className='form-control'
          />
        </FormGroup>

        <FormGroup title='' isRequired>
          <label className='radio-inline'>
            <input
              type='radio'
              name='sex'
              value={Const.Sex.Male}
              onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
              checked={PersonalInfoItem.sex === Const.Sex.Male}
            /> 
          </label>
          <label className='radio-inline'>
            <input
              type='radio'
              name='sex'
              value={Const.Sex.Female}
              onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
              checked={PersonalInfoItem.sex === Const.Sex.Female}
            /> 
          </label>
        </FormGroup>

        <FormGroup title='' isRequired>
          <div className='row'>
            <div className='col-sm-4 col-xs-4'>
              <div className='input-group'>
                <select
                  name='birthday_y'
                  className='form-control'
                  value={PersonalInfoItem.birthDate.year}
                  onChange={PersonalInfoHandlers.updateBirthDateHandler.bind(this, 'year')}
                >
                  {
                    InputHelper.GetBirthYearItems().map(item =>
                      <option key={item} value={item}>{item}</option>
                    )
                  }
                </select>
                <span className='input-group-addon'></span>
              </div>
            </div>
            <div className='col-sm-4 col-xs-4'>
              <div className='input-group'>
                <select
                  name='birthday_m'
                  className='form-control'
                  value={PersonalInfoItem.birthDate.month}
                  onChange={PersonalInfoHandlers.updateBirthDateHandler.bind(this, 'month')}
                >
                  {
                    InputHelper.GetMonthItems().map(item =>
                      <option key={item} value={item}>{item}</option>
                    )
                  }
                </select>
                <span className='input-group-addon'></span>
              </div>
            </div>
            <div className='col-sm-4 col-xs-4'>
              <div className='input-group'>
                <select
                  name='birthday_d'
                  className='form-control'
                  value={PersonalInfoItem.birthDate.date}
                  onChange={PersonalInfoHandlers.updateBirthDateHandler.bind(this, 'date')}
                >
                  {
                    InputHelper.GetDayItems().map(item =>
                      <option key={item} value={item}>{item}</option>
                    )
                  }
                </select>
                <span className='input-group-addon'></span>
              </div>
            </div>
          </div>
          {
            PersonalInfoItem.isValidBirthDate === false &&
              <AlertText text='' />
          }
        </FormGroup>

        <FormGroup title='' isRequired>
          <div className='btn-group btn-group-justified' data-toggle='buttons'>
            <label className={`btn btn-default ${isAdditionalContactTypeRelative === true ? 'active' : ''}`} id='btn_renraku_relative'>
              <input
                type='radio'
                name='kokyaku_renraku_2_type'
                value={Const.PersonalAdditionalContactType.Relative}
                onClick={PersonalInfoHandlers.additionalContactTypeClickHandler.bind(this)}
                autoComplete='off'
              /> 
            </label>
            <label className={`btn btn-default ${isAdditionalContactTypeCompany === true ? 'active' : ''}`} id='btn_renraku_company'>
              <input
                type='radio'
                name='kokyaku_renraku_2_type'
                value={Const.PersonalAdditionalContactType.Company}
                onClick={PersonalInfoHandlers.additionalContactTypeClickHandler.bind(this)}
                autoComplete='off'
              /> 
            </label>
          </div>
        </FormGroup>

        {
          isAdditionalContactTypeRelative === true &&
            <div id='renraku_relative'>
              <FormGroup>
                <div className='row'>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='relativeLastName'
                        value={PersonalInfoItem.relativeLastName}
                        onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='relativeFirstName'
                        value={PersonalInfoItem.relativeFirstName}
                        onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                </div>
              </FormGroup>

              <FormGroup>
                <div className='row'>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='relativeLastNameKana'
                        value={PersonalInfoItem.relativeLastNameKana}
                        onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='relativeFirstNameKana'
                        value={PersonalInfoItem.relativeFirstNameKana}
                        onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                </div>
              </FormGroup>

              <FormGroup>
                <PhoneNumber
                  title=''
                  phoneType={Const.PhoneType.RelativePhone}
                  phone1={PersonalInfoItem.relativePhone1}
                  phone2={PersonalInfoItem.relativePhone2}
                  phone3={PersonalInfoItem.relativePhone3}
                  updatePhoneHandler={PersonalInfoHandlers.updatePhoneHandler}
                />
                {
                  PersonalInfoItem.isValidRelativePhone === false &&
                    <AlertText text='' />
                }
                <p className='help-block'></p>
              </FormGroup>

              <FormGroup>
                <div className='input-group'>
                  <div className='input-group-addon'>&nbsp;</div>
                  <input
                    type='text'
                    name='relativeRelationship'
                    value={PersonalInfoItem.relativeRelationship}
                    onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                    size='25'
                    maxLength='30'
                    className='form-control'
                  />
                </div>
              </FormGroup>
            </div>
        }

        {
          isAdditionalContactTypeCompany === true &&
            <div id='renraku_company'>
              <FormGroup>
                <div className='input-group'>
                  <div className='input-group-addon'>    </div>
                  <input
                    type='text'
                    name='companyName'
                    value={PersonalInfoItem.companyName}
                    onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                    size='25'
                    maxLength='80'
                    className='form-control'
                  />
                </div>
              </FormGroup>

              <FormGroup>
                <div className='input-group'>
                  <div className='input-group-addon'></div>
                  <input
                    type='text'
                    name='companyNameKana'
                    value={PersonalInfoItem.companyNameKana}
                    onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                    size='25'
                    maxLength='80'
                    className='form-control'
                  />
                </div>
              </FormGroup>

              <FormGroup>
                <PhoneNumber
                  title=''
                  phoneType={Const.PhoneType.CompanyPhone}
                  phone1={PersonalInfoItem.companyPhone1}
                  phone2={PersonalInfoItem.companyPhone2}
                  phone3={PersonalInfoItem.companyPhone3}
                  updatePhoneHandler={PersonalInfoHandlers.updatePhoneHandler}
                />
                {
                  PersonalInfoItem.isValidCompanyPhone === false &&
                    <AlertText text='' />
                }
                <p className='help-block'></p>
              </FormGroup>
            </div>
        }
      </div>
    );
  }
}
