import React from 'react';

export default function PostalCode (props) {
  const {
    postalCode1,
    postalCode2,
    updatePostalCodeHandler
  } = props;

  var updateHandler = () => {};
  if (updatePostalCodeHandler !== undefined) {
    updateHandler = updatePostalCodeHandler;
  }
  return (
    <div className='row'>
      <div className='col-sm-6 col-xs-6'>
        <div className='input-group'>
          <input
            type='email'
            name='kokyaku_post_no'
            value={postalCode1}
            onChange={updateHandler.bind(this, 'postalCode1')}
            size='3'
            maxLength='3'
            className='form-control ime-mode-disabled'
          />
          <div className='input-group-addon'> - </div>
          <input
            type='email'
            name='kokyaku_post_no'
            value={postalCode2}
            onChange={updateHandler.bind(this, 'postalCode2')}
            size='4'
            maxLength='4'
            className='form-control ime-mode-disabled'
          />
        </div>
      </div>
    </div>
  );
}
