import React from 'react';
import FormGroup from '../Shared/FormGroup';
import PhoneNumber from '../Shared/PhoneNumber';
import PostalCode from '../Shared/PostalCode';
import AlertText from '../Shared/AlertText';
import * as Const from '../../Const';

export default class Entry extends React.Component {
  render () {
    const {
      CompanyInfoItem,
      CompanyInfoHandlers
    } = this.props;

    const isAdditionalContactTypePersonInCharge = (CompanyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.PersonInCharge);
    const isAdditionalContactTypeRelative = (CompanyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.Relative);

    // console.log(CompanyInfoItem, CompanyInfoItem.companyName);

    return (
      <div id='kokyaku_company'>
        <FormGroup title='' isRequired>
          <input
            type='text'
            name='companyName'
            value={CompanyInfoItem.companyName}
            onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
            size='40'
            maxLength='80'
            className='form-control'
          />
        </FormGroup>

        <FormGroup title='' isRequired>
          <input
            type='text'
            name='companyNameKana'
            value={CompanyInfoItem.companyNameKana}
            onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
            size='40'
            maxLength='120'
            className='form-control'
          />
        </FormGroup>

        <FormGroup title='' isRequired>
          <div className='row'>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'></span>
                <input
                  type='text'
                  name='repLastName'
                  value={CompanyInfoItem.repLastName}
                  onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                  size='20'
                  maxLength='40'
                  className='form-control'
                />
              </div>
            </div>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'></span>
                <input
                  type='text'
                  name='repFirstName'
                  value={CompanyInfoItem.repFirstName}
                  onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                  size='20'
                  maxLength='40'
                  className='form-control'
                />
              </div>
            </div>
          </div>
        </FormGroup>

        <FormGroup title='' isRequired>
          <div className='row'>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'></span>
                <input
                  type='text'
                  name='repLastNameKana'
                  value={CompanyInfoItem.repLastNameKana}
                  onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                  size='20'
                  maxLength='40'
                  className='form-control'
                />
              </div>
            </div>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'></span>
                <input
                  type='text'
                  name='repFirstNameKana'
                  value={CompanyInfoItem.repFirstNameKana}
                  onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                  size='20'
                  maxLength='40'
                  className='form-control'
                />
              </div>
            </div>
          </div>
        </FormGroup>

        <FormGroup title='' isRequired>
          <input
            type='email'
            name='kaisha_email'
            value={CompanyInfoItem.email}
            onChange={CompanyInfoHandlers.updateEmailHandler.bind(this)}
            size='30'
            maxLength='60'
            className='form-control ime-mode-disabled'
          />
          {
            CompanyInfoItem.isValidEmail === false &&
              <AlertText text='' />
          }
        </FormGroup>

        <FormGroup title='' isRequired>
          <input
            type='password'
            name='kokyaku_password'
            onChange={CompanyInfoHandlers.updatePasswordHandler.bind(this)}
            size='40'
            maxLength='60'
            className='form-control ime-mode-disabled'
          />
          {
            CompanyInfoItem.isValidPassword === false &&
              <AlertText text='' />
          }
          <p className='help-block'>7</p>
        </FormGroup>

        <FormGroup title='' isRequired>
          <PhoneNumber
            title=''
            phoneType={Const.PhoneType.Telephone}
            phone1={CompanyInfoItem.tel1}
            phone2={CompanyInfoItem.tel2}
            phone3={CompanyInfoItem.tel3}
            updatePhoneHandler={CompanyInfoHandlers.updatePhoneHandler}
          />
          {
            CompanyInfoItem.isValidTel === false &&
              <AlertText text='' />
          }
        </FormGroup>

        <FormGroup isRequired={false}>
          <PhoneNumber
            title='FAX'
            phoneType={Const.PhoneType.Fax}
            phone1={CompanyInfoItem.fax1}
            phone2={CompanyInfoItem.fax2}
            phone3={CompanyInfoItem.fax3}
            updatePhoneHandler={CompanyInfoHandlers.updatePhoneHandler}
          />
          {
            CompanyInfoItem.isValidFax === false &&
              <AlertText text='' />
          }
        </FormGroup>

        <FormGroup title='' isRequired>
          <PostalCode
            postalCode1={CompanyInfoItem.postalCode1}
            postalCode2={CompanyInfoItem.postalCode2}
            updatePostalCodeHandler={CompanyInfoHandlers.updateCompanyPostalCode}
          />
          {
            CompanyInfoItem.isValidPostalCode === false &&
              <AlertText text='' />
          }
        </FormGroup>

        <FormGroup title='' isRequired>
          <input
            type='text'
            name='address'
            value={CompanyInfoItem.address}
            onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
            size='56'
            maxLength='100'
            className='form-control'
          />
        </FormGroup>

        <FormGroup title='' isRequired>
          <div className='btn-group btn-group-justified' data-toggle='buttons'>
            <label className={`btn btn-default ${isAdditionalContactTypePersonInCharge === true ? 'active' : ''}`} id='btn_kaisha_tantou'>
              <input
                type='radio'
                name='kaisha_renraku_2_type'
                value={Const.CompanyAdditionalContactType.PersonInCharge}
                onClick={CompanyInfoHandlers.additionalContactTypeClickHandler.bind(this)}
                autoComplete='off'
              /> 
            </label>
            <label className={`btn btn-default ${isAdditionalContactTypeRelative === true ? 'active' : ''}`} id='btn_kaisha_shinzoku'>
              <input
                type='radio'
                name='kaisha_renraku_2_type'
                value={Const.CompanyAdditionalContactType.Relative}
                onClick={CompanyInfoHandlers.additionalContactTypeClickHandler.bind(this)}
                autoComplete='off'
              /> 
            </label>
          </div>
        </FormGroup>

        {
          isAdditionalContactTypePersonInCharge === true &&
            <div id='kaisha_tantou'>
              <FormGroup>
                <div className='row'>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='personInChargeLastName'
                        value={CompanyInfoItem.personInChargeLastName}
                        onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='personInChargeFirstName'
                        value={CompanyInfoItem.personInChargeFirstName}
                        onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                </div>
              </FormGroup>
              <FormGroup>
                <div className='row'>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='personInChargeLastNameKana'
                        value={CompanyInfoItem.personInChargeLastNameKana}
                        onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='personInChargeFirstNameKana'
                        value={CompanyInfoItem.personInChargeFirstNameKana}
                        onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                </div>
              </FormGroup>
              <FormGroup>
                <PhoneNumber
                  title=''
                  phoneType={Const.PhoneType.PersonInChargePhone}
                  phone1={CompanyInfoItem.personInChargePhone1}
                  phone2={CompanyInfoItem.personInChargePhone2}
                  phone3={CompanyInfoItem.personInChargePhone3}
                  updatePhoneHandler={CompanyInfoHandlers.updatePhoneHandler}
                />
                {
                  CompanyInfoItem.isValidPersonInChargePhone === false &&
                    <AlertText text='' />
                }
                <p className='help-block'></p>
              </FormGroup>
              <FormGroup>
                <div className='input-group'>
                  <div className='input-group-addon'>&nbsp;</div>
                  <input
                    type='text'
                    name='personInChargeDepartment'
                    value={CompanyInfoItem.personInChargeDepartment}
                    onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                    size='25'
                    maxLength='30'
                    className='form-control'
                  />
                </div>
              </FormGroup>
            </div>
        }
        {
          isAdditionalContactTypeRelative === true &&
            <div id='kaisha_shinzoku'>
              <FormGroup>
                <div className='row'>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='relativeLastName'
                        value={CompanyInfoItem.relativeLastName}
                        onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='relativeFirstName'
                        value={CompanyInfoItem.relativeFirstName}
                        onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                </div>
              </FormGroup>
              <FormGroup>
                <div className='row'>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='relativeLastNameKana'
                        value={CompanyInfoItem.relativeLastNameKana}
                        onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6 col-xs-6'>
                    <div className='input-group'>
                      <span className='input-group-addon'></span>
                      <input
                        type='text'
                        name='relativeFirstNameKana'
                        value={CompanyInfoItem.relativeFirstNameKana}
                        onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                        size='20'
                        maxLength='40'
                        className='form-control'
                      />
                    </div>
                  </div>
                </div>
              </FormGroup>
              <FormGroup>
                <PhoneNumber
                  title=''
                  phoneType={Const.PhoneType.RelativePhone}
                  phone1={CompanyInfoItem.relativePhone1}
                  phone2={CompanyInfoItem.relativePhone2}
                  phone3={CompanyInfoItem.relativePhone3}
                  updatePhoneHandler={CompanyInfoHandlers.updatePhoneHandler}
                />
                {
                  CompanyInfoItem.isValidRelativePhone === false &&
                    <AlertText text='' />
                }
                <p className='help-block'></p>
              </FormGroup>
              <FormGroup>
                <div className='input-group'>
                  <div className='input-group-addon'>&nbsp;&nbsp;</div>
                  <input
                    type='text'
                    name='relationship'
                    value={CompanyInfoItem.relationship}
                    onChange={CompanyInfoHandlers.updateCompanyInfoHandler.bind(this)}
                    size='25'
                    maxLength='30'
                    className='form-control'
                  />
                </div>
              </FormGroup>
            </div>
        }

        <FormGroup title='' isRequired>
          <p className='mb00'><small>2</small></p>
          <label className='radio-inline'>
            <input
              type='radio'
              name='kaisha_renraku_2_yuusen'
              onChange={CompanyInfoHandlers.switchAdditionalContactFirstHandler.bind(this, true)}
              checked={CompanyInfoItem.additionalContactFirst === true}
              value='1'
            /> 
          </label>
          <label className='radio-inline'>
            <input
              type='radio'
              name='kaisha_renraku_2_yuusen'
              onChange={CompanyInfoHandlers.switchAdditionalContactFirstHandler.bind(this, false)}
              checked={CompanyInfoItem.additionalContactFirst === false}
              value='2'
            /> 
          </label>
        </FormGroup>
      </div>
    );
  }
}
