import Axios from 'axios';
import * as Const from '../Const';
import * as Questionnaires from '../Questionnaires';

export function GetToken (authorization) {
  const url = `${window.storeganise_api_url}/v1/auth/token?include=settings,files`;
  const requestConfig = {
    headers: {
      Accept: 'application/json',
      Authorization: authorization
    }
  };
  return Axios.post(url, {}, requestConfig);
}

export function CreateUser (data, password) {
  const url = `${window.storeganise_api_url}/v1/users`;
  const requestData = GetUserMainData(data);
  const note = GetNote(data);

  // create unit order
  var startDate = `${data.startDateItem.startDate.year}-`;
  const month = data.startDateItem.startDate.month;
  const date = data.startDateItem.startDate.date;
  startDate = startDate + (month < 10 ? '0' : '') + month + '-';
  startDate = startDate + (date < 10 ? '0' : '') + date;

  const requestConfig = {
    headers: {
      'content-type': 'application/json'
    }
  };

  return Axios.post(url, { ...requestData, password, startDate, note, language: 'ja' }, requestConfig);
}

export function GetPassword (data) {
  var password = '';
  if (data.customerType === Const.CustomerType.Personal) {
    password = data.personalInfoItem.password;
  } else {
    password = data.companyInfoItem.password;
  }
  return password;
}

