export function UploadFile (title, file, accessToken) {
  const url = `${window.storeganise_api_url}/v1/user/files`;

  if (file !== undefined) {
    const formData = new FormData();
    formData.append('data', `{"title":{"en":"${title}","ja":"${title}"}}`);
    formData.append('file', file);

    const requestConfig = {
      headers: {
        'content-type': 'multipart/form-data',
        Authorization: `Bearer ${accessToken}`
      }
    };

    return Axios.post(url, formData, requestConfig);
  }
}

export function UploadCustomerInfoAsTextFile (data, accessToken) {
  const url = `${window.storeganise_api_url}/v1/user/files`;
  const customerInfo = GetNote(data);
  var file = new File([customerInfo], 'Customer Info.txt', {
    type: 'text/plain'
  });

  const formData = new FormData();
  formData.append('data', '{"title":{"en":"Customer Info","ja":"Customer Info"}}');
  formData.append('file', file);

  const requestConfig = {
    headers: {
      'content-type': 'multipart/form-data',
      Authorization: `Bearer ${accessToken}`
    }
  };

  return Axios.post(url, formData, requestConfig);
}

export function GetAvaliableUnit (shopId, unitTypeId) {
  const url = `${window.storeganise_api_url}/v1/admin/units?limit=1&offset=0&siteId=${shopId}&include=rental&state=available&typeId=${unitTypeId}`;
  const requestConfig = {
    headers: {
      'content-type': 'application/json',
      Authorization: ''
    }
  };

  return Axios.get(url, requestConfig);
}

export function CreateUnitOrder (shopId, startDate, unitTypeId, accessToken) {
  const url = `${window.storeganise_api_url}/v1/units/orders`;
  const requestData = {
    siteId: shopId,
    startDate,
    unitTypeId
  };
  const requestConfig = {
    headers: {
      'content-type': 'application/json',
      Authorization: `Bearer ${accessToken}`
    }
  };

  return Axios.post(url, requestData, requestConfig);
}

function GetUserMainData (data) {
  var altContactName = '';
  var altContactPhone = '';

  if (data.customerType === Const.CustomerType.Personal) {
    const personalInfo = data.personalInfoItem;
    const mobilePhone = `${personalInfo.mobilePhone1}${personalInfo.mobilePhone2}${personalInfo.mobilePhone3}`;
    const tel = `${personalInfo.tel1}${personalInfo.tel2}${personalInfo.tel3}`;
    const mobilePhoneToSave = `${personalInfo.mobilePhone1}-${personalInfo.mobilePhone2}-${personalInfo.mobilePhone3}`;
    const telToSave = `${personalInfo.tel1}-${personalInfo.tel2}-${personalInfo.tel3}`;
    const phone = (mobilePhone !== '' ? mobilePhoneToSave : telToSave);
    const phone2 = ((mobilePhone !== '' && tel !== '') ? telToSave : '');

    if (personalInfo.additionalContactType === Const.PersonalAdditionalContactType.Relative) {
      altContactName = `${personalInfo.relativeLastName} ${personalInfo.relativeFirstName}`;
      altContactPhone = `${personalInfo.relativePhone1}-${personalInfo.relativePhone2}-${personalInfo.relativePhone3}`;
    }
    return {
      email: personalInfo.email,
      address: `${personalInfo.postalCode1}-${personalInfo.postalCode2}\r\n${personalInfo.address}`,
      firstName: personalInfo.firstName,
      lastName: personalInfo.lastName,
      phone,
      phone2,
      altContactName,
      altContactPhone
    };
  } else {
    const companyInfoItem = data.companyInfoItem;

    const tel = `${companyInfoItem.tel1}-${companyInfoItem.tel2}-${companyInfoItem.tel3}`;

    if (companyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.PersonInCharge) {
      altContactName = `${companyInfoItem.personInChargeLastName} ${companyInfoItem.personInChargeFirstName}`;
      altContactPhone = `${companyInfoItem.personInChargePhone1}-${companyInfoItem.personInChargePhone2}-${companyInfoItem.personInChargePhone3}`;
    }

    return {
      address: `${companyInfoItem.postalCode1}-${companyInfoItem.postalCode2}\r\n${companyInfoItem.address}`,
      email: companyInfoItem.email,
      firstName: companyInfoItem.repFirstName,
      lastName: companyInfoItem.repLastName,
      companyName: companyInfoItem.companyName,
      phone: tel,
      altContactName,
      altContactPhone
    };
  }
}

function GetNote (data) {
  const items = [];

  items.push(`\t${data.shopUnitInfo.shopName}`);
  items.push(`\t${data.shopUnitInfo.unitTypeName}`);
  items.push(`\t${data.startDateItem.startDate.year}${data.startDateItem.startDate.month}${data.startDateItem.startDate.date}`);

  const overallItem = data.overallItem;
  if (overallItem.paymentMethod === Const.PaymentMethod.CreditCard) {
    items.push('\t');
  } else if (overallItem.paymentMethod === Const.PaymentMethod.BankTransfer) {
    items.push('\t');
  }

  if (data.customerType === Const.CustomerType.Personal) {
    const personalInfo = data.personalInfoItem;
    items.push('-');

    items.push(`\t${personalInfo.lastName} ${personalInfo.firstName}`);
    items.push(`\t${personalInfo.lastNameKana} ${personalInfo.firstNameKana}`);
    items.push(`\t${personalInfo.email}`);

    const mobilePhone = `${personalInfo.mobilePhone1}-${personalInfo.mobilePhone2}-${personalInfo.mobilePhone3}`;
    const tel = `${personalInfo.tel1}-${personalInfo.tel2}-${personalInfo.tel3}`;
    items.push(` - \t${mobilePhone}`);
    items.push(` - \t${tel}`);

    const fax = `${personalInfo.fax1}-${personalInfo.fax2}-${personalInfo.fax3}`;
    items.push(` - FAX\t${fax}`);

    const postalCode = `${personalInfo.postalCode1}-${personalInfo.postalCode2}`;
    items.push(`\t${postalCode}`);
    items.push(`\t${personalInfo.address}`);

    const sex = (personalInfo.sex === Const.Sex.Male ? '' : (personalInfo.sex === Const.Sex.Female ? '' : ''));
    items.push(`\t${sex}`);

    const birthDate = `${personalInfo.birthDate.year}/${personalInfo.birthDate.month}/${personalInfo.birthDate.date}`;
    items.push(`\t${birthDate}`);

    if (personalInfo.additionalContactType === Const.PersonalAdditionalContactType.Relative) {
      items.push(' - ');
      items.push(`\t${personalInfo.relativeLastName} ${personalInfo.relativeFirstName}`);
      items.push(`\t${personalInfo.relativeLastNameKana} ${personalInfo.relativeFirstNameKana}`);

      const relativePhone = `${personalInfo.relativePhone1}-${personalInfo.relativePhone2}-${personalInfo.relativePhone3}`;
      items.push(`\t${relativePhone}`);
      items.push(`\t${personalInfo.relativeRelationship}`);
    }

    if (personalInfo.additionalContactType === Const.PersonalAdditionalContactType.Company) {
      items.push(' - ');
      items.push(`\t${personalInfo.companyName}`);
      items.push(`\t${personalInfo.companyNameKana}`);

      const companyPhone = `${personalInfo.companyPhone1}-${personalInfo.companyPhone2}-${personalInfo.companyPhone3}`;
      items.push(`\t${companyPhone}`);
    }
  } else if (data.customerType === Const.CustomerType.Company) {
    const companyInfoItem = data.companyInfoItem;
    items.push('-');

    items.push(`\t${companyInfoItem.companyName}`);
    items.push(`\t${companyInfoItem.companyNameKana}`);
    items.push(`\t${companyInfoItem.repLastName} ${companyInfoItem.repFirstName}`);
    items.push(`\t${companyInfoItem.repLastNameKana} ${companyInfoItem.repFirstNameKana}`);
    items.push(`\t${companyInfoItem.email}`);

    const tel = `${companyInfoItem.tel1}-${companyInfoItem.tel2}-${companyInfoItem.tel3}`;
    items.push(`\t${tel}`);

    const fax = `${companyInfoItem.fax1}-${companyInfoItem.fax2}-${companyInfoItem.fax3}`;
    items.push(`FAX\t${fax}`);

    const postalCode = `${companyInfoItem.postalCode1}-${companyInfoItem.postalCode2}`;
    items.push(`\t${postalCode}`);
    items.push(`\t${companyInfoItem.address}`);

    if (companyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.PersonInCharge) {
      items.push(' - ');
      items.push(`\t${companyInfoItem.personInChargeLastName} ${companyInfoItem.personInChargeFirstName}`);
      items.push(`()\t${companyInfoItem.personInChargeLastNameKana} ${companyInfoItem.personInChargeFirstNameKana}`);

      const personInChargePhone = `${companyInfoItem.personInChargePhone1}-${companyInfoItem.personInChargePhone2}-${companyInfoItem.personInChargePhone3}`;
      items.push(`\t${personInChargePhone}`);
      items.push(`\t${companyInfoItem.personInChargeDepartment}`);
    }

    if (companyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.Relative) {
      items.push(' - ');
      items.push(`\t${companyInfoItem.relativeLastName} ${companyInfoItem.relativeFirstName}`);
      items.push(`()\t${companyInfoItem.relativeLastNameKana} ${companyInfoItem.relativeFirstNameKana}`);

      const relativePhone = `${companyInfoItem.relativePhone1}-${companyInfoItem.relativePhone2}-${companyInfoItem.relativePhone3}`;
      items.push(`\t${relativePhone}`);
      items.push(`\t${companyInfoItem.relationship}`);
    }
  }

  items.push(`\t${overallItem.usagePeriod}`);
  items.push(`\t${overallItem.otherCampaign}`);

  var storageItems = Questionnaires.StorageItems.filter(item =>
    overallItem.storageItems.indexOf(item) > -1).join(',');
  items.push(`\t${storageItems}`);
  items.push(`()\t${overallItem.storageItemOther}`);

  items.push(`\t${overallItem.purpose}`);
  items.push(`()\t${overallItem.purposeOther}`);

  items.push(`\t${overallItem.viewedMedias}`);
  items.push(`()\t${overallItem.viewedMediaOther}`);

  var rentalReasons = Questionnaires.RentalReasons.filter(item =>
    overallItem.rentalReasons.indexOf(item) > -1).join(',');
  items.push(`\t${rentalReasons}`);
  items.push(`()\t${overallItem.rentalReasonOther}`);

  items.push(`\t${overallItem.otherMessage}`);

  var note = '';
  items.forEach(element => {
    note += `${element}\r\n`;
  });

  return note;
}
