import React from 'react';
import { Button, Modal } from 'react-bootstrap';

export default class TermsAndConditions extends React.Component {
  constructor () {
    super();

    this.toggleAgreed = this.toggleAgreed.bind(this);

    this.state = {
      agreed: false
    };
  }

  toggleAgreed (event) {
    this.setState({ agreed: event.target.checked });
  }

  render () {
    const {
      HideTermsAndConditionsHandler,
      ConfirmTermsAndConditionsHandler,
      ConfirmedTermsAndConditions
    } = this.props;

    const showModal = (ConfirmedTermsAndConditions !== true);
    return (
      <Modal show={showModal} onHide={() => {}}>
        <Modal.Header>
          <Modal.Title><strong></strong></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            <strong>  </strong>
            <br />
            <br />
            <strong>JPEGPDF</strong>
            <br />
            <br />
            <a href='/entry/terms_and_conditions/honnin.html' target='_blank' rel='noopener noreferrer'></a>
            <br />
            <br />
            <strong></strong>
          </p>

          <ul className='list-unstyled'>
            <li><a href='/entry/terms_and_conditions/oshiire_kojin_jouhou.html' target='_blank' rel='noopener noreferrer'></a></li>
            <li><a href='/entry/terms_and_conditions/oshiire_yakkan.html' target='_blank' rel='noopener noreferrer'></a></li>
            <li><a href='/entry/terms_and_conditions/kojin_jouhou.html' target='_blank' rel='noopener noreferrer'></a></li>
            <li><a href='/entry/terms_and_conditions/hoshou_yakkan.html' target='_blank' rel='noopener noreferrer'></a></li>
          </ul>

          <div className='checkbox'>
            <label>
              <input type='checkbox' id='check' defaultChecked={false} onChange={this.toggleAgreed.bind(this)} /> 
            </label>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={HideTermsAndConditionsHandler}></Button>
          <Button bsStyle='primary' disabled={!this.state.agreed} onClick={ConfirmTermsAndConditionsHandler}></Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
