import React from 'react';
import '../App.css';
import EntryRows from './EntryComponents/EntryRows';
import Header from './Shared/Header';
import TermsAndConditions from './EntryComponents/TermsAndConditions';
import uuidv4 from 'uuid/v4';
import qs from 'query-string';
import Axios from 'axios';
import * as Storeganise from '../Utils/Storeganise';
import * as InputHelper from '../Utils/EntryInputHelper';
import * as Const from '../Const';
import * as InitValue from '../Utils/InitValue';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import 'react-block-ui/style.css';
import 'loaders.css/loaders.min.css';

export default class Entry extends React.Component {
  constructor () {
    super();

    this.updateStartDate = this.updateStartDate.bind(this);
    this.updateBirthDate = this.updateBirthDate.bind(this);
    this.updatePersonalEmail = this.updatePersonalEmail.bind(this);
    this.updateCompanyEmail = this.updateCompanyEmail.bind(this);

    this.updatePersonalPassword = this.updatePersonalPassword.bind(this);
    this.updateCompanyPassword = this.updateCompanyPassword.bind(this);

    this.updatePersonalInfo = this.updatePersonalInfo.bind(this);
    this.updatePersonalPhone = this.updatePersonalPhone.bind(this);
    this.updatePersonalPostalCode = this.updatePersonalPostalCode.bind(this);

    this.paymentMethodClickHandler = this.paymentMethodClickHandler.bind(this);
    this.customerTypeClickHandler = this.customerTypeClickHandler.bind(this);
    this.personalAdditionalContactTypeClickHandler = this.personalAdditionalContactTypeClickHandler.bind(this);

    this.updateCompanyInfo = this.updateCompanyInfo.bind(this);
    this.updateCompanyPhone = this.updateCompanyPhone.bind(this);
    this.updateCompanyPostalCode = this.updateCompanyPostalCode.bind(this);

    this.companyAdditionalContactTypeClickHandler = this.companyAdditionalContactTypeClickHandler.bind(this);
    this.companySwitchAdditionalContactFirst = this.companySwitchAdditionalContactFirst.bind(this);

    this.identificationChangeHandler = this.identificationChangeHandler.bind(this);
    this.updateOverallInfoHandler = this.updateOverallInfoHandler.bind(this);
    this.overallInfoSelectChangeHandler = this.overallInfoSelectChangeHandler.bind(this);
    this.overallInfoCheckboxClickHandler = this.overallInfoCheckboxClickHandler.bind(this);

    this.submitHandler = this.submitHandler.bind(this);

    this.hideTermsAndConditionsHandler = this.hideTermsAndConditionsHandler.bind(this);
    this.confirmTermsAndConditionsHandler = this.confirmTermsAndConditionsHandler.bind(this);

    this.state = {
      confirmedTermsAndConditions: false,
      shopUnitInfo: undefined,
      missingParameter: false,
      loadShopInfoError: undefined,
      loadUnitTypeError: undefined,
      postDataErrors: undefined,
      orderCreated: false,
      validationErrors: undefined,
      submitting: false,
      customerType: Const.CustomerType.Personal,
      startDateItem: {
        startDate: InputHelper.GetCurrentDate(),
        isValid: true,
        updateStartDateHandler: this.updateStartDate
      },
      identification1: undefined,
      identification2: undefined,
      overallItem: InitValue.InitOverallItem,
      personalInfoItem: InitValue.InitPersonalInfoItem,
      companyInfoItem: InitValue.InitCompanyInfoItem
      // overallItem: InitValue.TestOverallItem,
      // personalInfoItem: InitValue.TestPersonalInfoItem,
      // companyInfoItem: InitValue.TestCompanyInfoItem
    };
  }

  componentDidMount () {
    this.setState({ loadShopInfoError: undefined });
    this.setState({ loadUnitTypeError: undefined });

    var shopId = qs.parse(this.props.location.search, { ignoreQueryPrefix: true }).shopid;
    var unitTypeId = qs.parse(this.props.location.search, { ignoreQueryPrefix: true }).unittypeid;

    if (shopId === undefined || shopId.trim() === '') {
      this.setState({ missingParameter: true });
      return;
    } else {
      this.setState({ missingParameter: false });
    }

    if (unitTypeId === undefined || unitTypeId.trim() === '') {
      this.setState({ missingParameter: true });
      return;
    } else {
      this.setState({ missingParameter: false });
    }

    shopId = shopId.trim();
    unitTypeId = unitTypeId.trim();

    const url = `${window.storeganise_api_url}/v1/settings`;
    Axios.get(url, { headers: { Accept: 'application/json, text/plain, */*' } }).then(response => {
      const shopUnitInfo = InputHelper.GetShopUnitInfo(response.data, shopId, unitTypeId);
      if (shopUnitInfo.shopIdValid === true && shopUnitInfo.unitTypeIdValid === true) {
        this.setState({ shopUnitInfo });
      } else if (shopUnitInfo.shopIdValid === false) {
        this.setState({ loadShopInfoError: '' });
      } else if (shopUnitInfo.unitTypeIdValid === false) {
        this.setState({ loadUnitTypeError: '' });
      }
    }).catch(error => {
      console.log(error.config);
      this.setState({ loadShopInfoError: '' });
    });
  }

  getOverallHandlers () {
    return {
      identificationChangeHandler: this.identificationChangeHandler,
      customerTypeClickHandler: this.customerTypeClickHandler,
      paymentMethodClickHandler: this.paymentMethodClickHandler,
      updateOverallInfoHandler: this.updateOverallInfoHandler,
      selectChangeHandler: this.overallInfoSelectChangeHandler,
      checkboxClickHandler: this.overallInfoCheckboxClickHandler
    };
  }

  getPersonalInfoHandlers () {
    return {
      updatePersonalInfoHandler: this.updatePersonalInfo,
      updateEmailHandler: this.updatePersonalEmail,
      updatePasswordHandler: this.updatePersonalPassword,
      updateBirthDateHandler: this.updateBirthDate,
      updatePhoneHandler: this.updatePersonalPhone,
      updatePersonalPostalCodeHandler: this.updatePersonalPostalCode,
      additionalContactTypeClickHandler: this.personalAdditionalContactTypeClickHandler
    };
  }

  getCompanyInfoHandlers () {
    return {
      updateEmailHandler: this.updateCompanyEmail,
      updatePasswordHandler: this.updateCompanyPassword,
      updateCompanyInfoHandler: this.updateCompanyInfo,
      updatePhoneHandler: this.updateCompanyPhone,
      updateCompanyPostalCode: this.updateCompanyPostalCode,
      additionalContactTypeClickHandler: this.companyAdditionalContactTypeClickHandler,
      switchAdditionalContactFirstHandler: this.companySwitchAdditionalContactFirst
    };
  }

  paymentMethodClickHandler (event) {
    const currentData = JSON.parse(JSON.stringify(this.state.overallItem));
    const newItem = { paymentMethod: event.target.value };
    this.setState({ overallItem: { ...currentData, ...newItem } });
  }

  customerTypeClickHandler (event) {
    this.setState({ customerType: event.target.value });
  }

  personalAdditionalContactTypeClickHandler (event) {
    const currentData = JSON.parse(JSON.stringify(this.state.personalInfoItem));
    this.setState({ personalInfoItem: { ...currentData, additionalContactType: event.target.value } });
  }

  updateStartDate (field, event) {
    const currentDateItem = JSON.parse(JSON.stringify(this.state.startDateItem));
    const newDate = { ...currentDateItem.startDate, [field]: Number(event.target.value) };
    const isValid = InputHelper.IsValidDate(newDate.year, newDate.month, newDate.date);
    this.setState({
      startDateItem: {
        startDate: newDate,
        isValid,
        updateStartDateHandler: this.updateStartDate
      }
    });
  }

  updateBirthDate (field, event) {
    const currentData = JSON.parse(JSON.stringify(this.state.personalInfoItem));
    const newDate = { ...currentData.birthDate, [field]: Number(event.target.value) };
    const isValid = InputHelper.IsValidDate(newDate.year, newDate.month, newDate.date);
    const newData = { ...currentData, birthDate: newDate, isValidBirthDate: isValid };
    this.setState({ personalInfoItem: newData });
  }

  updatePersonalEmail (event) {
    const email = event.target.value;
    const currentData = JSON.parse(JSON.stringify(this.state.personalInfoItem));

    if (email === '') {
      const newData = { ...currentData, email: '', isValidEmail: undefined };
      this.setState({ personalInfoItem: newData });
    } else {
      const newData = { ...currentData, email, isValidEmail: Const.EmailRex.test(email) };
      this.setState({ personalInfoItem: newData });
    }
  }

  updatePersonalPassword (event) {
    const password = event.target.value;
    const currentData = JSON.parse(JSON.stringify(this.state.personalInfoItem));

    if (password === '') {
      const newData = { ...currentData, password: '', isValidPassword: undefined };
      this.setState({ personalInfoItem: newData });
    } else {
      const newData = { ...currentData, password, isValidPassword: Const.PasswordRex.test(password) };
      this.setState({ personalInfoItem: newData });
    }
  }

  updatePersonalInfo (event) {
    const currentData = JSON.parse(JSON.stringify(this.state.personalInfoItem));
    const newData = { ...currentData, [event.target.name]: event.target.value };
    this.setState({ personalInfoItem: newData });
  }

  updatePersonalPhone (phoneType, field, event) {
    const currentData = JSON.parse(JSON.stringify(this.state.personalInfoItem));
    var newData;
    var fieldName;

    const phoneIndex = field.replace('phone', '');
    if (phoneType === Const.PhoneType.MobilePhone) {
      fieldName = `mobilePhone${phoneIndex}`;
    } else if (phoneType === Const.PhoneType.Telephone) {
      fieldName = `tel${phoneIndex}`;
    } else if (phoneType === Const.PhoneType.Fax) {
      fieldName = `fax${phoneIndex}`;
    } else if (phoneType === Const.PhoneType.RelativePhone) {
      fieldName = `relativePhone${phoneIndex}`;
    } else if (phoneType === Const.PhoneType.CompanyPhone) {
      fieldName = `companyPhone${phoneIndex}`;
    }

    if (fieldName !== undefined) {
      newData = { ...currentData, [fieldName]: event.target.value };
      var mobilePhone = `${newData.mobilePhone1}${newData.mobilePhone2}${newData.mobilePhone3}`;
      var telephone = `${newData.tel1}${newData.tel2}${newData.tel3}`;
      var fax = `${newData.fax1}${newData.fax2}${newData.fax3}`;
      var relativePhone = `${newData.relativePhone1}${newData.relativePhone2}${newData.relativePhone3}`;
      var companyPhone = `${newData.companyPhone1}${newData.companyPhone2}${newData.companyPhone3}`;

      var isValidMobilePhone = (mobilePhone === '' ? undefined : Const.NumberRex.test(mobilePhone));
      var isValidTel = (telephone === '' ? undefined : Const.NumberRex.test(telephone));
      var isValidFax = (fax === '' ? undefined : Const.NumberRex.test(fax));
      var isValidRelativePhone = (relativePhone === '' ? undefined : Const.NumberRex.test(relativePhone));
      var isValidCompanyPhone = (companyPhone === '' ? undefined : Const.NumberRex.test(companyPhone));

      this.setState({
        personalInfoItem: {
          ...newData,
          isValidMobilePhone,
          isValidTel,
          isValidFax,
          isValidRelativePhone,
          isValidCompanyPhone
        }
      });
    }
  }

  updatePersonalPostalCode (field, event) {
    const currentData = JSON.parse(JSON.stringify(this.state.personalInfoItem));
    const newData = { ...currentData, [field]: event.target.value.trim() };

    var isValidPostalCode;
    if (newData.postalCode1 !== '' || newData.postalCode2 !== '') {
      if (newData.postalCode1.length !== 3 || newData.postalCode2.length !== 4) {
        isValidPostalCode = false;
      } else {
        var postalCode = `${newData.postalCode1}${newData.postalCode2}`;
        isValidPostalCode = Const.NumberRex.test(postalCode);
      }
    }

    this.setState({ personalInfoItem: { ...newData, isValidPostalCode } });
  }

  // Company Info related
  updateCompanyInfo (event) {
    const currentData = JSON.parse(JSON.stringify(this.state.companyInfoItem));
    const newData = { ...currentData, [event.target.name]: event.target.value };
    this.setState({ companyInfoItem: newData });
  }

  updateCompanyEmail (event) {
    const email = event.target.value;
    const currentData = JSON.parse(JSON.stringify(this.state.companyInfoItem));

    if (email === '') {
      const newData = { ...currentData, email: '', isValidEmail: undefined };
      this.setState({ companyInfoItem: newData });
    } else {
      const newData = { ...currentData, email, isValidEmail: Const.EmailRex.test(email) };
      this.setState({ companyInfoItem: newData });
    }
  }

  updateCompanyPassword (event) {
    const password = event.target.value;
    const currentData = JSON.parse(JSON.stringify(this.state.companyInfoItem));

    if (password === '') {
      const newData = { ...currentData, password: '', isValidPassword: undefined };
      this.setState({ companyInfoItem: newData });
    } else {
      const newData = { ...currentData, password, isValidPassword: Const.PasswordRex.test(password) };
      this.setState({ companyInfoItem: newData });
    }
  }

  updateCompanyPhone (phoneType, field, event) {
    const currentData = JSON.parse(JSON.stringify(this.state.companyInfoItem));
    var newData;
    var fieldName;

    const phoneIndex = field.replace('phone', '');
    if (phoneType === Const.PhoneType.Telephone) {
      fieldName = `tel${phoneIndex}`;
    } else if (phoneType === Const.PhoneType.Fax) {
      fieldName = `fax${phoneIndex}`;
    } else if (phoneType === Const.PhoneType.PersonInChargePhone) {
      fieldName = `personInChargePhone${phoneIndex}`;
    } else if (phoneType === Const.PhoneType.RelativePhone) {
      fieldName = `relativePhone${phoneIndex}`;
    }

    if (fieldName !== undefined) {
      newData = { ...currentData, [fieldName]: event.target.value };
      var telephone = `${newData.tel1}${newData.tel2}${newData.tel3}`;
      var fax = `${newData.fax1}${newData.fax2}${newData.fax3}`;
      var personInChargePhone = `${newData.personInChargePhone1}${newData.personInChargePhone2}${newData.personInChargePhone3}`;
      var relativePhone = `${newData.relativePhone1}${newData.relativePhone2}${newData.relativePhone3}`;

      var isValidTel = (telephone === '' ? undefined : Const.NumberRex.test(telephone));
      var isValidFax = (fax === '' ? undefined : Const.NumberRex.test(fax));
      var isValidPersonInChargePhone = (personInChargePhone === '' ? undefined : Const.NumberRex.test(personInChargePhone));
      var isValidRelativePhone = (relativePhone === '' ? undefined : Const.NumberRex.test(relativePhone));

      this.setState({
        companyInfoItem: {
          ...newData,
          isValidTel,
          isValidFax,
          isValidPersonInChargePhone,
          isValidRelativePhone
        }
      });
    }
  }

  companyAdditionalContactTypeClickHandler (event) {
    const currentData = JSON.parse(JSON.stringify(this.state.companyInfoItem));
    this.setState({ companyInfoItem: { ...currentData, additionalContactType: event.target.value } });
  }

  companySwitchAdditionalContactFirst (additionalContactFirst) {
    const currentData = JSON.parse(JSON.stringify(this.state.companyInfoItem));
    this.setState({ companyInfoItem: { ...currentData, additionalContactFirst } });
  }

  identificationChangeHandler (event) {
    if (event.target.files !== undefined && event.target.files.length === 1) {
      this.setState({ [event.target.name]: event.target.files[0] });
    } else {
      this.setState({ [event.target.name]: undefined });
    }
  }

  updateOverallInfoHandler (event) {
    const currentData = JSON.parse(JSON.stringify(this.state.overallItem));
    const newItem = { [event.target.name]: event.target.value };
    this.setState({ overallItem: { ...currentData, ...newItem } });
  }

  overallInfoSelectChangeHandler (field, selectedItem) {
    const currentData = JSON.parse(JSON.stringify(this.state.overallItem));
    const newItem = { [field]: selectedItem.value };
    this.setState({ overallItem: { ...currentData, ...newItem } });
  }

  overallInfoCheckboxClickHandler (event) {
    const currentData = JSON.parse(JSON.stringify(this.state.overallItem));
    var currentItems = currentData[event.target.name];
    if (event.target.checked === true) {
      currentItems.push(event.target.value);
      const newItem = { [event.target.name]: currentItems };
      this.setState({ overallItem: { ...currentData, ...newItem } });
    } else {
      const newItems = [];
      currentItems.forEach(element => {
        if (element !== event.target.value) {
          newItems.push(element);
        }
      });
      const newItem = { [event.target.name]: newItems };
      this.setState({ overallItem: { ...currentData, ...newItem } });
    }
  }

  updateCompanyPostalCode (field, event) {
    const currentData = JSON.parse(JSON.stringify(this.state.companyInfoItem));
    const newData = { ...currentData, [field]: event.target.value.trim() };

    var isValidPostalCode;
    if (newData.postalCode1 !== '' || newData.postalCode2 !== '') {
      if (newData.postalCode1.length !== 3 || newData.postalCode2.length !== 4) {
        isValidPostalCode = false;
      } else {
        var postalCode = `${newData.postalCode1}${newData.postalCode2}`;
        isValidPostalCode = Const.NumberRex.test(postalCode);
      }
    }

    this.setState({ companyInfoItem: { ...newData, isValidPostalCode } });
  }

  submitHandler () {
    window.scrollTo(0, 0);
    const validationResult = InputHelper.ValidateInput(this.state);
    this.setState({ postDataErrors: undefined });

    const password = Storeganise.GetPassword(this.state);

    if (validationResult.valid === true) {
      this.setState({ validationErrors: undefined, submitting: true });
      // create a new user
      const createUser = Storeganise.CreateUser(this.state, password);
      createUser.then(response => {
        const userEmailPassword = `${response.data.email}:${password}`;
        const base64UserEmailPassword = window.btoa(userEmailPassword);
        const authorization = `Basic ${base64UserEmailPassword}`;

        // get user token
        const getToken = Storeganise.GetToken(authorization);
        getToken.then(response => {
          const accessToken = response.data.accessToken;
          this.uploadDoc1(accessToken);
        }).catch(error => {
          this.setState({ submitting: false });
          console.log(error);
          this.setPostDataErrors('');
        });
      }).catch(error => {
        this.setState({ submitting: false });
        if (error.response) {
          if (error.response.status === 400 && error.response.data.error.message === 'Email already exists') {
            this.setPostDataErrors('');
          } else {
            this.setPostDataErrors('');
          }
        } else {
          this.setPostDataErrors('');
        }
      });
    } else {
      window.scrollTo(0, 0);
      this.setState({ validationErrors: validationResult.errors });
    }
  }

  uploadDoc1 (accessToken) {
    const uploadFilePromise = Storeganise.UploadFile('Frontside', this.state.identification1, accessToken);
    uploadFilePromise.then(response => {
      this.uploadDoc2(accessToken);
    }).catch(error => {
      this.setState({ submitting: false });
      console.log(error);
      this.setPostDataErrors('');
    });
  }

  uploadDoc2 (accessToken) {
    if (this.state.identification2 !== undefined) {
      const uploadFilePromise = Storeganise.UploadFile('Backside', this.state.identification2, accessToken);
      uploadFilePromise.then(response => {
        this.uploadCustomerInfoAsTextFile(accessToken);
      }).catch(error => {
        this.setState({ submitting: false });
        console.log(error);
        this.setPostDataErrors('');
      });
    } else {
      this.uploadCustomerInfoAsTextFile(accessToken);
    }
  }

  uploadCustomerInfoAsTextFile (accessToken) {
    const uploadCustomerInfoPromise = Storeganise.UploadCustomerInfoAsTextFile(this.state, accessToken);
    uploadCustomerInfoPromise.then(response => {
      this.createOrder(accessToken);
    }).catch(error => {
      this.setState({ submitting: false });
      console.log(error);
      this.setPostDataErrors('');
    });
  }

  createOrder (accessToken) {
    // create unit order
    var startDate = `${this.state.startDateItem.startDate.year}-`;
    const month = this.state.startDateItem.startDate.month;
    const date = this.state.startDateItem.startDate.date;
    startDate = startDate + (month < 10 ? '0' : '') + month + '-';
    startDate = startDate + (date < 10 ? '0' : '') + date;

    const createUnitOrder = Storeganise.CreateUnitOrder(this.state.shopUnitInfo.shopId, startDate, this.state.shopUnitInfo.unitTypeId, accessToken);
    createUnitOrder.then(response => {
      this.setState({ orderCreated: true, submitting: false });
    }).catch(error => {
      this.setState({ submitting: false });
      console.log(error);
      this.setPostDataErrors('');
    });
  }

  setPostDataErrors (message) {
    this.setState({ postDataErrors: message });
  }

  hideTermsAndConditionsHandler () {
    window.history.back();
  }

  confirmTermsAndConditionsHandler () {
    this.setState({ confirmedTermsAndConditions: true });
  }

  render () {
    return (
      <>
        <Header />
        <article className='service'>
          <section className='contents'>
            <div className='container'>
              <div id='errors' className='row'>
                <div className='col-md-8 col-md-offset-2 col-sm-10 col-sm-offset-1 validation-error'>
                  {
                    this.state.missingParameter === true &&
                      <p></p>
                  }
                  {
                    this.state.loadShopInfoError !== undefined &&
                      <p>{this.state.loadShopInfoError}</p>
                  }
                  {
                    this.state.loadUnitTypeError !== undefined &&
                      <p>{this.state.loadUnitTypeError}</p>
                  }
                  {
                    this.state.validationErrors !== undefined &&
                        this.state.validationErrors.map(item => { return (<p key={uuidv4()}>{item}</p>); })
                  }

                  {
                    this.state.postDataErrors !== undefined &&
                      <p>{this.state.postDataErrors}</p>
                  }
                </div>

              </div>
              {
                this.state.orderCreated === true &&
                  <div id='order_created' className='row'>
                    <div className='col-md-8 col-md-offset-2 col-sm-12'>
                      <div className='thank-you-header'></div>
                      <div className='thank-you-body'>
                        <div></div>
                        <div></div>
                        <div></div>
                      </div>
                    </div>
                  </div>
              }

              {
                this.state.submitting === true &&
                  <BlockUi
                    blocking={this.state.submitting}
                    loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
                  >
                    <div id='order_creating' className='row order-creating-container'>
                      <div className='col-md-8 col-md-offset-2 col-sm-12'>
                        <div className='order-creating-body'>
                          <p align='center'>...</p>
                          <p align='center'>...</p>
                        </div>
                      </div>
                    </div>
                  </BlockUi>
              }

              {
                this.state.orderCreated !== true &&
                  <EntryRows
                    ShopUnitInfo={this.state.shopUnitInfo}
                    StartDateItem={this.state.startDateItem}
                    CompanyInfoItem={this.state.companyInfoItem}
                    PersonalInfoItem={this.state.personalInfoItem}
                    OverallItem={this.state.overallItem}
                    CustomerType={this.state.customerType}
                    OverallHandlers={this.getOverallHandlers()}
                    PersonalInfoHandlers={this.getPersonalInfoHandlers()}
                    CompanyInfoHandlers={this.getCompanyInfoHandlers()}
                    SubmitHandler={this.submitHandler}
                  />
              }
            </div>
          </section>
        </article>

        <TermsAndConditions
          HideTermsAndConditionsHandler={this.hideTermsAndConditionsHandler}
          ConfirmTermsAndConditionsHandler={this.confirmTermsAndConditionsHandler}
          ConfirmedTermsAndConditions={this.state.confirmedTermsAndConditions}
        />
      </>
    );
  }
}
