import React from 'react';

export default function PhoneNumber (props) {
  const {
    title,
    phoneType,
    phone1,
    phone2,
    phone3,
    updatePhoneHandler
  } = props;

  var updateHandler = () => {};
  if (updatePhoneHandler !== undefined) {
    updateHandler = updatePhoneHandler;
  }

  return (
    <div className='input-group'>
      <div className='input-group-addon'>{title}</div>
      <input
        type='email'
        name='phone1'
        value={phone1}
        onChange={updateHandler.bind(this, phoneType, 'phone1')}
        size='5'
        maxLength='5'
        className='form-control ime-mode-disabled'
      />
      <div className='input-group-addon'> - </div>
      <input
        type='email'
        name='phone2'
        value={phone2}
        onChange={updateHandler.bind(this, phoneType, 'phone2')}
        size='5'
        maxLength='5'
        className='form-control ime-mode-disabled'
      />
      <div className='input-group-addon'> - </div>
      <input
        type='email'
        name='phone3'
        value={phone3}
        onChange={updateHandler.bind(this, phoneType, 'phone3')}
        size='5'
        maxLength='5'
        className='form-control ime-mode-disabled'
      />
    </div>
  );
}
